
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters, mapActions } from "vuex";
import userAPI from "@/api/user.js";
import memberAPI from "@/api/membershipCard";

export default {
components: {
XDialog,
Toast
},
name: "login",
data() {
return {
isError: false,
errorMsg: "",
j_verificationcode: "",
j_username: "",
j_password: "",
inputShow: true,
showPassword: false, //显示密码
showdialog: false, //显示验证码弹框
checkImage: "./images/checkImage.png",
showToast: false 
};
},
methods: {
...mapActions(["setUser", "setMemberNumber"]),
login: function() {
let validate = this.validate();
if (validate) {
let pasKey = JSON.stringify(
this.keyEncrypt(this.j_password, this.publicKey)
);
let params = {
j_username: this.j_username,
j_verificationcode: this.j_verificationcode,
j_password: pasKey,
responseType: "json"
};
userAPI.login(params).then(res => {
let data = res.data;
let code = data.code;

if (code == 200) {
this.setUser(data.user);
this.saveMemberNumber();

if (this.$route.query.redirect) {
let redirect = this.$route.query.redirect;
this.$router.push(redirect);
} else {
this.$router.push("/");
}
} else if (code == 500) {
let errorMsg = data.errorMsg;
var startReg = new RegExp("^UserName:");
if (errorMsg == "Bad credentials") {
errorMsg = "您输入的密码不正确,请核对之后在进行登录";
} else if (startReg.test(errorMsg)) {
errorMsg = "您输入的用户名不存在,请核对之后在进行登录";
}

if (!this.showdialog && data.showVerificationCode) {
this.j_verificationcode = "";
this.showdialog = true;
this.getKaptcha();
} else {
this.showdialog = false;
this.errorMsg = errorMsg;
this.isError = true;
}
}
});
}
},
saveMemberNumber() {
memberAPI.getCurrentMemberInfo().then(res => {
console.log(res);
let data = res.data;
this.setMemberNumber(data.result);
});
},
keyEncrypt(data, coll) {
const rsaEncrypt = new JSEncrypt();
rsaEncrypt.setPublicKey(coll); 
const passPhrase = this.generateEncryptPassword(32);

const iv = CryptoJS.lib.WordArray.random(128 / 8).toString(
CryptoJS.enc.Hex
);
const salt = CryptoJS.lib.WordArray.random(128 / 8).toString(
CryptoJS.enc.Hex
);
const key = CryptoJS.PBKDF2(passPhrase, CryptoJS.enc.Hex.parse(salt), {
keySize: 128 / 32,
iterations: 1000
});

const aesEncrypted = CryptoJS.AES.encrypt(data, key, {
iv: CryptoJS.enc.Hex.parse(iv)
});
const aesKey = passPhrase + ":::" + salt + ":::" + aesEncrypted.iv;
const encryptedMessage = aesEncrypted.ciphertext.toString(
CryptoJS.enc.Base64
);
const encryptedKey = rsaEncrypt.encrypt(aesKey);

const encrypted = encryptedKey + ":::" + encryptedMessage;
return encrypted;
},

generateEncryptPassword(length) {
const encryptPassChars =
"0123456789ABCDEFGHIJKLMNOPQRSTUVWXTZabcdefghiklmnopqrstuvwxyz*&-%/!?*+=()";
let randomstring = "";
for (let i = 0; i < length; i++) {
const rnum = Math.floor(Math.random() * encryptPassChars.length);
randomstring += encryptPassChars.substring(rnum, rnum + 1);
}
return randomstring;
},
getKaptcha: function() {
this.checkImage = "/Kaptcha.jpg?v=" + new Date().getTime();
},
getPublicKey: function() {
userAPI.getPublicKey({}).then(res => {
this.publicKey = res.data.result;
});
},

cancelIcon() {
this.j_username = "";
},

changePasseord() {
if (this.j_password === "") {
this.showPassword = false;
}
if (this.j_password !== "") {
this.showPassword = true;
}
},
validate: function() {
let validate = true;

if (this.j_username == "") {
validate = false;
this.isNullUserName = true;
}

if (this.j_password == "") {
validate = false;
this.isNullPassword = true;
}
return validate;
},

changeType() {
this.inputShow = !this.inputShow;
},
cancelDialog() {
this.showdialog = false;
},

changeToast() {
this.showToast = true;
}
},
watch: {
j_password(newValue) {
this.changePasseord();
},
j_username(newValue) {
console.log(newValue);
}
},
computed: {
...mapGetters(["getUser"])
},
created() {},
mounted() {
this.getPublicKey();
let elem = document.querySelector("#vux_view_box_body");
setTimeout(() => {
elem.style.paddingBottom = "0";
}, 10);
}
};
